/* src/app/modules/notifications/notifications.component.scss */
.notifications-container .notification-item {
  transition: all 0.3s ease;
}
.notifications-container .notification-item:hover {
  background-color: rgba(var(--bs-primary-rgb), 0.05);
}
.notifications-container .notification-item.unread {
  background-color: rgba(var(--bs-primary-rgb), 0.1);
  border-left: 3px solid var(--bs-primary);
}
.notifications-container .notification-item.recent {
  animation: pulse 2s infinite;
}
.notifications-container .notification-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
}
.notifications-container .notification-icon.priority-info {
  background-color: rgba(var(--bs-primary-rgb), 0.1);
  color: var(--bs-primary);
}
.notifications-container .notification-icon.priority-warning {
  background-color: rgba(var(--bs-warning-rgb), 0.1);
  color: var(--bs-warning);
}
.notifications-container .notification-icon.priority-error {
  background-color: rgba(var(--bs-danger-rgb), 0.1);
  color: var(--bs-danger);
}
.notifications-container .notification-icon.priority-critical {
  background-color: rgba(var(--bs-dark-rgb), 0.1);
  color: var(--bs-dark);
}
.notifications-container .notification-badge {
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
}
.notifications-container .notification-badge.status-en-attente {
  background-color: rgba(var(--bs-warning-rgb), 0.1);
  color: var(--bs-warning);
}
.notifications-container .notification-badge.status-envoye {
  background-color: rgba(var(--bs-info-rgb), 0.1);
  color: var(--bs-info);
}
.notifications-container .notification-badge.status-delivre {
  background-color: rgba(var(--bs-primary-rgb), 0.1);
  color: var(--bs-primary);
}
.notifications-container .notification-badge.status-lu {
  background-color: rgba(var(--bs-success-rgb), 0.1);
  color: var(--bs-success);
}
.notifications-container .notification-badge.status-echec {
  background-color: rgba(var(--bs-danger-rgb), 0.1);
  color: var(--bs-danger);
}
.notifications-container .notification-actions {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.notification-item:hover .notifications-container .notification-actions {
  opacity: 1;
}
.notifications-container .stats-card {
  transition: transform 0.3s ease;
}
.notifications-container .stats-card:hover {
  transform: translateY(-5px);
}
.notifications-container .filters-modal .modal-content {
  border-radius: 12px;
  border: none;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}
.notifications-container .filters-modal .modal-header {
  border-bottom: 1px solid var(--bs-border-color);
  padding: 1.5rem;
}
.notifications-container .filters-modal .modal-header .modal-title {
  font-weight: 600;
  color: var(--bs-heading-color);
}
.notifications-container .filters-modal .modal-body {
  padding: 1.5rem;
}
.notifications-container .filters-modal .modal-footer {
  border-top: 1px solid var(--bs-border-color);
  padding: 1rem 1.5rem;
}
.notifications-container .confirmation-modal .modal-content {
  border-radius: 12px;
  border: none;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}
.notifications-container .confirmation-modal .modal-body {
  padding: 2rem;
}
.notifications-container .confirmation-modal .modal-body img {
  max-width: 80px;
  margin: 0 auto 1.5rem;
}
.notifications-container .confirmation-modal .modal-body h6 {
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--bs-heading-color);
}
.notifications-container .confirmation-modal .modal-body p {
  color: var(--bs-secondary-color);
  margin-bottom: 1.5rem;
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(var(--bs-primary-rgb), 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(var(--bs-primary-rgb), 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(var(--bs-primary-rgb), 0);
  }
}
@media (max-width: 768px) {
  .notifications-container .stats-card {
    margin-bottom: 1rem;
  }
  .notifications-container .table-responsive {
    font-size: 0.875rem;
  }
  .notifications-container .table-responsive th,
  .notifications-container .table-responsive td {
    padding: 0.5rem;
  }
  .notifications-container .table-responsive .notification-actions {
    opacity: 1;
  }
  .notifications-container .modal-dialog {
    margin: 0.5rem;
  }
  .notifications-container .modal-dialog.modal-dialog-centered {
    min-height: calc(100% - 1rem);
  }
}
[data-bs-theme=dark] .notifications-container .notification-item:hover {
  background-color: rgba(255, 255, 255, 0.05);
}
[data-bs-theme=dark] .notifications-container .notification-item.unread {
  background-color: rgba(var(--bs-primary-rgb), 0.15);
}
[data-bs-theme=dark] .notifications-container .notification-icon.priority-info {
  background-color: rgba(var(--bs-primary-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-icon.priority-warning {
  background-color: rgba(var(--bs-warning-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-icon.priority-error {
  background-color: rgba(var(--bs-danger-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-icon.priority-critical {
  background-color: rgba(var(--bs-dark-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-badge.status-en-attente {
  background-color: rgba(var(--bs-warning-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-badge.status-envoye {
  background-color: rgba(var(--bs-info-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-badge.status-delivre {
  background-color: rgba(var(--bs-primary-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-badge.status-lu {
  background-color: rgba(var(--bs-success-rgb), 0.2);
}
[data-bs-theme=dark] .notifications-container .notification-badge.status-echec {
  background-color: rgba(var(--bs-danger-rgb), 0.2);
}
